import React, { Component } from "react";
import styles from "./index.css";
import CSSModules from "react-css-modules";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import TabsContent from "../Components/TabsContent";
// import Typography from "@material-ui/core/Typography";

class MyOrder extends Component {
  constructor(props) {
    super(props);
    document.title = "我的订单";
    this.state = {
      value: 0,
      orderList: [
        {
          orderNumber: "L123489654521122",
          projectName: "基本保险续保",
          stauts: 1,
          price: 200
        },
        {
          orderNumber: "L123489654521122",
          projectName: "基本保险续保",
          stauts: 1,
          price: 200
        },
        {
          orderNumber: "L123489654521122",
          projectName: "基本保险续保",
          stauts: 1,
          price: 200
        }
      ]
    };
  }
  handleChange = (event, value) => {
    let orderList = [
      {
        orderNumber: "L123489654521122",
        projectName: "基本保险续保",
        stauts: 1,
        price: 200
      },
      {
        orderNumber: "L123489654521122",
        projectName: "基本保险续保",
        stauts: 1,
        price: 200
      }
    ];
    this.setState({ value: value, orderList: orderList });
  };
  render() {
    const { value } = this.state;

    return (
      <div>
        <AppBar
          position="static"
          //boxShadow: "none"
          style={{
            backgroundColor: "#fff",
            color: "#000",
            boxShadow: "none",
            padding: 0
          }}
        >
          <Tabs
            textColor="primary"
            indicatorColor="primary"
            variant="fullWidth"
            value={value}
            onChange={this.handleChange}
          >
            <Tab label="全部" />
            <Tab label="待支付" />
            <Tab label="已完成" />
            {/* <Tab label="已退款" /> */}
          </Tabs>
        </AppBar>
        {value === 0 && <TabsContent orderList={this.state.orderList} />}
        {value === 1 && <TabsContent orderList={this.state.orderList} />}
        {value === 2 && <TabsContent orderList={this.state.orderList} />}
        {value === 3 && <TabsContent orderList={this.state.orderList} />}
      </div>
    );
  }
}

CSSModules(TabsContent, styles);

export default CSSModules(MyOrder, styles);



