import React, { Component } from "react";
import styles from "./index.css";
import CSSModules from "react-css-modules";
import Divider from "@material-ui/core/Divider";
import Modal from "@material-ui/core/Modal";
import ComponentsSwiper from "../Components/ComponentsSwiper";
import {CopyToClipboard} from 'react-copy-to-clipboard';


class CollegeStudentHome extends Component {
  constructor(props) {
    super(props);
    document.title = "首页";
    this.state = {
      open: false,
      phoneListOpen: false,
      Copyvalue:"",
      phoneList: [
        { name: "张小龙", number: 15145698754 },
        { name: "李小强", number: 13256984554 },
        { name: "张晓磊", number: 13245697896 }
      ]
    };
  }
  handleChange = name => event => {
    console.log(name);
    this.setState({
      [name]: event.target.value
    });
  };
  linkTo = () => {
    this.props.history.push("/ParentInformation");
  };
  getHelp = () => {
    this.setState({
      open: true
    });
  };
  closeModal = type => {
    this.setState({
      open: false
    });
  };
  openClassSalesman = () => {
    this.setState({
      phoneListOpen: true
    });
  };
  callSalesman = data => {
    console.log(data);
    // window.location.href = "tel:" + data.number;
    this.setState({
      phoneListOpen: false
    });
  };
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="CollegeStudentHome">
        <div styleName="CollegeStudentHome_top">
          <div styleName="CollegeStudentHome_top_content">
            <div styleName="CollegeStudentHome_top_item">
              <span styleName="CollegeStudentHome_top_item_title">
                学生姓名
              </span>
              <span styleName="CollegeStudentHome_top_item_content">
                周星星
              </span>
              {/* <div styleName="CollegeStudentHome_top_item_line" /> */}
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="CollegeStudentHome_top_item">
              <span styleName="CollegeStudentHome_top_item_title">
                身份证号
              </span>
              <span styleName="CollegeStudentHome_top_item_content">
                230198756213215865
              </span>
              {/* <div styleName="CollegeStudentHome_top_item_line" /> */}
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="CollegeStudentHome_top_item">
              <span styleName="CollegeStudentHome_top_item_title">
                联系电话
              </span>
              <span styleName="CollegeStudentHome_top_item_content">
                15045891653
              </span>
              {/* <div styleName="CollegeStudentHome_top_item_line" /> */}
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="CollegeStudentHome_top_item">
              <span styleName="CollegeStudentHome_top_item_title">
                所在学校
              </span>
              <span styleName="CollegeStudentHome_top_item_content">
                黑龙江大学
              </span>
            </div>
          </div>
        </div>
        <ComponentsSwiper
          router={this.props}
          type={"CollegeStudentHome"}
          openClassSalesman={this.openClassSalesman}
        />
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.open}
          styleName="CollegeStudentHome_modal"
          onClick={() => {
            this.closeModal();
          }}
        >
          <div styleName="CollegeStudentHome_modalDiv">
            <span
              className="iconfont icon-xiangzuohuadong"
              styleName="CollegeStudentHome_button_icon"
            />
            <p styleName="CollegeStudentHome_modalDiv_span">
              学生信息可左右滑动
            </p>
          </div>
        </Modal>
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.phoneListOpen}
          styleName="CollegeStudentHome_modal"
        >
          <div styleName="phoneListModal">
            <div styleName="phoneListModal_content">
              {this.state.phoneList.map((v, i) => {
                return (
                  <div
                    styleName="CollegeStudentHome_top_item1"
                    key={i}
                    // onClick={() => this.callSalesman(v)}
                  >
                    <span styleName="CollegeStudentHome_top_item_title1">
                      {v.name}
                    </span>
                    <span styleName="CollegeStudentHome_top_item_content1">
                      {v.number}
                    </span>
                    <CopyToClipboard text={v.number} >
                      <div onClick={() => this.callSalesman(v)} styleName="copyBtn">复制</div>
                    </CopyToClipboard>
                    {/* <span
                      className="iconfont icon-xiangyoujiantou"
                      styleName="CollegeStudentHome_top_item_icon1"
                    /> */}
                    {
                      <Divider
                        style={{
                          backgroundColor:
                            i === this.state.phoneList.length - 1
                              ? "transparent"
                              : "#B5B5B6"
                        }}
                      />
                    }
                  </div>
                );
              })}
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}

export default CSSModules(CollegeStudentHome, styles);



