import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Divider from '@material-ui/core/Divider';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

class ApplyRefund extends Component {
  constructor(props) {
    super(props);
    document.title = '申请退款';
    this.state = {
      checkList: [],
      isInputCode:false,
      reason: '',
      inputCode:'',
      timerList:[],
      changeColor:[],
      startTime:'2019-01-01 00:00:00',
      endTime:'2019-12-31 23:59:59',
      code:'L12535523265',
      InsuranceList: [
        {
          title: "城乡居民基本医疗保险",
          time: {
            fistTime: '2019-08-08 08:00:00',
            lastTime: '2019-08-08 08:00:00'
          },
          price: 260,
          status:1,
        },
        {
          title: "学生儿童补充医疗保险(短期)",
          time: {
            fistTime: '2019-08-08 08:00:00',
            lastTime: '2019-08-08 08:00:00'
          },
          price: 260,
          status:0,
        }
      ]
    };
  }
  handleChange = name => event => {
    if(name === 'inputCode'){
      if(event.target.value === '' || event.target.value == null){
        this.setState({
          [name]: event.target.value,
          isInputCode:false
        });
      }else{
        this.setState({
          [name]: event.target.value,
          isInputCode:true
        });
      }
    }
  };
  isChoice = index => {
    console.log(index)
    let list = this.state.checkList;
    list[index] = !list[index];
    this.setState({
      checkList: list
    });
  };
  componentDidMount() {
    let checkList = [];
    let timerList = [];
    let changeColor = [];
    // let timer1 = setInterval(()=>{
    //   console.log("timer1")
    // },200)
    let _this = this;
    this.state.InsuranceList.map((v, i) => {
      checkList[i] = false;
      changeColor[i] = true
      timerList[i] = setInterval(()=>{
        changeColor[i] = !changeColor[i]
        _this.setState({
          changeColor : changeColor
        })
        console.log(_this.state.changeColor)
      },700)
      return false;
    });
    console.log(timerList)
    this.setState({
      checkList: checkList,
      timerList:timerList
    });
  }
  componentWillUnmount() {
    this.state.timerList.map((v,i)=>{
      clearInterval(v);
      return false;
    })
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="ApplyRefund">
        <div styleName="ApplyRefund_top">
          <div styleName="ApplyRefund_top_content">
            <div styleName="ApplyRefund_top_item">
              <span styleName="ApplyRefund_top_item_title">姓名</span>
              <span styleName="ApplyRefund_top_item_content">张金</span>
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="ApplyRefund_top_item">
              <span styleName="ApplyRefund_top_item_title">身份证号</span>
              <span styleName="ApplyRefund_top_item_content">
                2301558948954415
              </span>
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="ApplyRefund_top_item">
              <span styleName="ApplyRefund_top_item_title">手机号</span>
              <span styleName="ApplyRefund_top_item_content">13945687412</span>
            </div>
          </div>
        </div>
        <div>
          {this.state.InsuranceList.map((v, i) => {
            return (
              <div styleName="ApplyRefund_top1" key={i}>
                
                {v.status === 0 && <div
                  styleName="ApplyRefund_iconBox"
                  onClick={() => {
                      this.isChoice(i);
                  }}
                >
                  <div
                    styleName="triangle-topright"
                    style={{
                      borderColor: this.state.checkList[i]
                      ? '#009B63 transparent transparent #009B63' : this.state.changeColor[i] ? '#D5D3FC transparent transparent #D5D3FC' : '#B5B5B6 transparent transparent #B5B5B6'
                    }}
                  />
                  <span className="iconfont icon-dui" styleName="editIcon" />
                </div>}


                  <p styleName="ApplyRefund_content_item_p">
                   <span styleName="ApplyRefund_content_item_span">
                     保险名称&nbsp;&nbsp;
                   </span>
                   {v.title}
                 </p>
                 <p styleName="ApplyRefund_content_item_p">
                   <span styleName="ApplyRefund_content_item_span">
                     开始期间&nbsp;&nbsp;
                   </span>
                   {v.time.fistTime}
                 </p>
                 <p styleName="ApplyRefund_content_item_p">
                   <span styleName="ApplyRefund_content_item_span">
                     结束期间&nbsp;&nbsp;
                   </span>
                   {v.time.lastTime}
                 </p>
                 <p styleName="ApplyRefund_content_item_p">
                   <span styleName="ApplyRefund_content_item_span">
                     购买金额&nbsp;&nbsp;
                   </span>
                   ¥{v.price}
                 </p>
                 {v.status === 1 && <p styleName="ApplyRefund_content_item_p1">注：此产品已下保险单，不可申请退单</p>}
                {/* <div styleName="ApplyRefund_top_item">
                  <span styleName="ApplyRefund_top_item_title1">
                    基本医疗保险续费
                  </span>
                  <Divider style={{ backgroundColor: '#B5B5B6' }} />
                </div>
                <div styleName="ApplyRefund_top_item">
                  <span styleName="ApplyRefund_top_item_title">保险时间</span>
                  <div styleName="ApplyRefund_top_item_time">
                    <span styleName="ApplyRefund_top_item_content1">
                      开始时间&nbsp;{v.time.fistTime}
                    </span>
                    <span styleName="ApplyRefund_top_item_content1">
                      截止时间&nbsp;{v.time.lastTime}
                    </span>
                  </div>
                  <Divider style={{ backgroundColor: '#B5B5B6' }} />
                </div>
                <div styleName="ApplyRefund_top_item">
                  <span styleName="ApplyRefund_top_item_title">保险总费用</span>
                  <span styleName="ApplyRefund_top_item_content">
                    {v.price}元
                  </span>
                </div> */}
              
              </div>
            );
          })}
        </div>
        <div styleName="ApplyRefund_submitBox1">
          <TextField
            styleName="ApplyRefund_input1"
            placeholder="请输入退款原因"
            label={
              <span
                className="iconfont icon-shiliangzhinengduixiang"
                style={{ fontSize: '21px' }}
              >
                <span style={{ fontSize: '21px' }}>退款原因</span>
              </span>
            }
            multiline
            rows="4"
            value={this.state.reason}
            InputProps={{ style: { width: 'inherit', height: 'inherit' } }}
            InputLabelProps={{ shrink: true }}
            onChange={this.handleChange('reason')}
            margin="normal"
            variant="outlined"
          />
        </div>
        <div styleName="ApplyRefund_top1">
            <p styleName="ApplyRefund_content_item_p">
              <span styleName="ApplyRefund_content_item_span">
                退单码&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              </span>
              <span style={{color:'#009B63'}}>{this.state.code}</span>
            </p>
            <div styleName="showBtn">查看</div> 
            <p styleName="ApplyRefund_content_item_p">
              <span styleName="ApplyRefund_content_item_span">
                开始期间&nbsp;&nbsp;
              </span>
              {this.state.startTime}
            </p>
            <p styleName="ApplyRefund_content_item_p">
              <span styleName="ApplyRefund_content_item_span">
                结束期间&nbsp;&nbsp;
              </span>
              {this.state.endTime}
            </p>
            <p styleName="ApplyRefund_content_item_p" style={{color:'#E60012',fontSize:"12px"}}>
            友情提示：请认真选择需要退订的产品，并在下方填写退单码，退单码有效期为一个自然日。过期后不可操作退单
            </p>
        </div>
        <div styleName="ApplyRefund_submitBox">
        <div styleName="ApplyRefund_priceBox">
            <span>退款金额</span>
            <div styleName="ApplyRefund_priceBox_number">
              ¥&nbsp;
              <span styleName="ApplyRefund_priceBox_numberSpan">600</span>.00元
            </div>
          </div>
          <TextField
            styleName="ApplyRefund_input"
            placeholder="请输入退单码"
            label={
              <span
                className="iconfont icon-shiliangzhinengduixiang"
                style={{ fontSize: '21px' }}
              >
                <span style={{ fontSize: '21px' }}>退单码</span>
              </span>
            }
            multiline
            value={this.state.inputCode}
            InputProps={{ style: { width: 'inherit', height: 'inherit' } }}
            InputLabelProps={{ shrink: true }}
            onChange={this.handleChange('inputCode')}
            margin="normal"
            variant="outlined"
          />
          <Button
            variant="contained"
            style={{
              color: '#fff',
              textDecoration: 'none',
              backgroundColor: this.state.isInputCode
              ? '#009B63'
              : '#B5B5B6'
            }}
            styleName="ApplyRefund_submitBox_button"
            onClick={() => {}}
          >
            提交退款
          </Button>
        </div>
      </div>
    );
  }
}

export default CSSModules(ApplyRefund, styles);



