import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Divider from '@material-ui/core/Divider';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';

class ParentInformation extends Component {
  constructor(props) {
    super(props);
    document.title = '学生信息';
    this.state = {
      studentName: '周星星',
      idCardNumber: '23060612364587',
      affiliatedInstitutions: '哈尔滨音乐学院',
      grade: '三年级',
      studentClass: '7班',
      nation: '汉族',
      scholasticYearSystem: '三年制',
      isCollegeStudent: true,
      isEdit: true
    };
  }
  // handleChange = name => event => {
  //   console.log(name);
  //   this.setState({
  //     [name]: event.target.value
  //   });
  // };
  linkTo = () => {
    this.props.history.push('/ParentHome');
  };
  changeIpnut = (event, name) => {
    if (name === 'studentName') {
      this.setState({ studentName: event.target.value });
    } else if (name === 'idCardNumber') {
      this.setState({ idCardNumber: event.target.value });
    }
  };
  UniversityChoice = () => {
    if (!this.state.isEdit) {
      let datas = JSON.parse(
        localStorage.getItem('ParentInformation_formData')
      );
      console.log(datas);
      if (datas === null) {
        let dataKeysList = [
          'studentName',
          'idCardNumber',
          'grade',
          'studentClass',
          'scholasticYearSystem',
          'nation',
          'isEdit'
        ];
        datas = {};
        let _this = this;
        dataKeysList.map((v, i) => {
          datas[v] = _this.state[v];
          return false;
        });
        localStorage.setItem(
          'ParentInformation_formData',
          JSON.stringify(datas)
        );
      }
      this.props.history.push({
        pathname: '/UniversityChoice',
        pageName: 'ParentInformation'
      });
    }
  };
  editorread = () => {
    this.setState({
      isEdit: !this.state.isEdit,
      isCollegeStudent: !this.state.isCollegeStudent
    });
    if (this.state.isEdit === true) {
      // 获取焦点
      let inputFocus = document.getElementById(
        'ParentInformation_firstInput'
      );
      inputFocus.focus();
    }
  };
  componentDidMount() {
    let datas = localStorage.getItem('ParentInformation_formData');
    if (datas !== null) {
      let formDatas = JSON.parse(datas);
      console.log(formDatas);
      this.setState(formDatas);
      localStorage.removeItem('ParentInformation_formData');
    }
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="ParentInformation">
        <div styleName="parentMessageInfo">
          <div styleName="parentMessageInfo_content">
          <div styleName="parentMessageInfo_item">
              <span styleName="parentMessageInfo_item_title">
                学生姓名
              </span>
              <span styleName="parentMessageInfo_item_content">
                王昌龄
              </span>
              {/* <div styleName="parentMessageInfo_item_line" /> */}
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="parentMessageInfo_item">
              <span styleName="parentMessageInfo_item_title">
                身份证号
              </span>
              <span styleName="parentMessageInfo_item_content">
                230198756213215865
              </span>
              {/* <div styleName="parentMessageInfo_item_line" /> */}
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="parentMessageInfo_item">
              <span styleName="parentMessageInfo_item_title">
                联系电话
              </span>
              <span styleName="parentMessageInfo_item_content1">
                13265894587
              </span>
            </div>
          </div>
        </div>
        <div styleName="ParentInformation_top">
            
            <div styleName="ParentInformation_top_content">
              <div onClick={this.editorread}>
                <div
                  styleName="triangle-topright"
                  style={{
                    borderColor: this.state.isEdit
                      ? '#009B63 transparent transparent #009B63'
                      : '#F39800 transparent transparent #F39800'
                  }}
                />
                {this.state.isEdit ? (
                  <span
                    className="iconfont icon-shiliangzhinengduixiang"
                    styleName="editIcon"
                  />
                ) : (
                  <span className="iconfont icon-save" styleName="editIcon" />
                )}
              </div>
              <div styleName="ParentInformation_top_item">
              <span styleName="ParentInformation_top_item_title">
                学生姓名
              </span>

              <Input
                readOnly={this.state.isEdit}
                value={this.state.studentName}
                id="ParentInformation_firstInput"
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'studentName');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
              <div styleName="ParentInformation_top_item">
              <span styleName="ParentInformation_top_item_title">
                身份证号
              </span>
              <Input
                readOnly={this.state.isEdit}
                value={this.state.idCardNumber}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'idCardNumber');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
              <div
              styleName="ParentInformation_top_item"
              onClick={this.UniversityChoice}
            >
              <span styleName="ParentInformation_top_item_title">
                所在院校
              </span>
              {/*<Input
                readOnly={true}
                onClick={this.UniversityChoice}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                value={this.state.affiliatedInstitutions}
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'affiliatedInstitutions');
                }}
              />*/}
              <span
                styleName="ParentInformation_top_item_content"
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
              >
                {this.state.affiliatedInstitutions}
              </span>
             </div>
            </div>
          
            <div styleName="ParentInformation_top_content">
              <div onClick={this.editorread}>
                <div
                  styleName="triangle-topright"
                  style={{
                    borderColor: this.state.isEdit
                      ? '#009B63 transparent transparent #009B63'
                      : '#F39800 transparent transparent #F39800'
                  }}
                />
                {this.state.isEdit ? (
                  <span
                    className="iconfont icon-shiliangzhinengduixiang"
                    styleName="editIcon"
                  />
                ) : (
                  <span className="iconfont icon-save" styleName="editIcon" />
                )}
              </div>
              <div styleName="ParentInformation_top_item">
              <span styleName="ParentInformation_top_item_title">
                学生姓名
              </span>

              <Input
                readOnly={this.state.isEdit}
                value={this.state.studentName}
                id="ParentInformation_firstInput"
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'studentName');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
              <div styleName="ParentInformation_top_item">
              <span styleName="ParentInformation_top_item_title">
                身份证号
              </span>
              <Input
                readOnly={this.state.isEdit}
                value={this.state.idCardNumber}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'idCardNumber');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
              <div
              styleName="ParentInformation_top_item"
              onClick={this.UniversityChoice}
            >
              <span styleName="ParentInformation_top_item_title">
                所在院校
              </span>
              {/*<Input
                readOnly={true}
                onClick={this.UniversityChoice}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                value={this.state.affiliatedInstitutions}
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'affiliatedInstitutions');
                }}
              />*/}
              <span
                styleName="ParentInformation_top_item_content"
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
              >
                {this.state.affiliatedInstitutions}
              </span>
             </div>
            </div>

            <div styleName="ParentInformation_top_content">
              <div onClick={this.editorread}>
                <div
                  styleName="triangle-topright"
                  style={{
                    borderColor: this.state.isEdit
                      ? '#009B63 transparent transparent #009B63'
                      : '#F39800 transparent transparent #F39800'
                  }}
                />
                {this.state.isEdit ? (
                  <span
                    className="iconfont icon-shiliangzhinengduixiang"
                    styleName="editIcon"
                  />
                ) : (
                  <span className="iconfont icon-save" styleName="editIcon" />
                )}
              </div>
              <div styleName="ParentInformation_top_item">
              <span styleName="ParentInformation_top_item_title">
                学生姓名
              </span>

              <Input
                readOnly={this.state.isEdit}
                value={this.state.studentName}
                id="ParentInformation_firstInput"
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'studentName');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
              <div styleName="ParentInformation_top_item">
              <span styleName="ParentInformation_top_item_title">
                身份证号
              </span>
              <Input
                readOnly={this.state.isEdit}
                value={this.state.idCardNumber}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'idCardNumber');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
              <div
              styleName="ParentInformation_top_item"
              onClick={this.UniversityChoice}
            >
              <span styleName="ParentInformation_top_item_title">
                所在院校
              </span>
              {/*<Input
                readOnly={true}
                onClick={this.UniversityChoice}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                value={this.state.affiliatedInstitutions}
                styleName="ParentInformation_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'affiliatedInstitutions');
                }}
              />*/}
              <span
                styleName="ParentInformation_top_item_content"
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
              >
                {this.state.affiliatedInstitutions}
              </span>
             </div>
            </div>

          </div>
          <div styleName="btnBox">
            <Button
               variant="contained"
               color="primary"
               style={{ marginTop: '10px', marginBottom: '20px' }}
               styleName="ParentInformation_button"
               onClick={this.linkTo}
             >
               绑定
             </Button>
          </div>
      </div>
    );
  }
}

export default CSSModules(ParentInformation, styles);



