import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Button from '@material-ui/core/Button';
import Divider from "@material-ui/core/Divider";

class PaySuccess extends Component {
  constructor(props) {
    super(props);
    document.title = '支付成功';
    this.state = {};
  }
  linkTo = () => {};
  render() {
    return (
      <div styleName="PaySuccess">
        <span
          className="iconfont icon-zhifuchenggong"
          styleName="PaySuccess_Icon"
        />
        <div styleName="PaySuccess_top">
          <div>
            <div styleName="PaySuccess_top_item">
              <span styleName="PaySuccess_top_item_title">订单编号</span>
              <span styleName="PaySuccess_top_item_content">L123542789342123</span>
              {/* <div styleName="PaySuccess_top_item_line" /> */}
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="PaySuccess_top_item">
              <span styleName="PaySuccess_top_item_title">支付金额</span>
              <span styleName="PaySuccess_top_item_content">
                200.00
              </span>
            </div>
          </div>
        </div>
        <div styleName="btnBox">
        <Button
          variant="contained"
          color="primary"
          style={{ marginTop: '10px' }}
          styleName="PaySuccess_button"
          onClick={this.linkTo}
        >
          首页
        </Button>
        <Button
          variant="outlined"
          color="primary"
          style={{ marginTop: '10px' }}
          styleName="PaySuccess_button1"
          onClick={this.linkTo}
        >
          查看订单
        </Button>
        </div>
      </div>
    );
  }
}

export default CSSModules(PaySuccess, styles);



