import React, { Component } from "react";
import styles from "./index.css";
import CSSModules from "react-css-modules";
import Divider from "@material-ui/core/Divider";
class AlreadyRefund extends Component {
  constructor(props) {
    super(props);
    document.title = "订单详情";
    this.state = {
      insuranceList: [
        {
          time: {
            fistTime: "2019-08-08 08:00:00",
            lastTime: "2019-08-08 08:00:00"
          },
          price: 260
        },
        {
          time: {
            fistTime: "2019-08-08 08:00:00",
            lastTime: "2019-08-08 08:00:00"
          },
          price: 260
        }
      ]
    };
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="AlreadyRefund">
        <div styleName="AlreadyRefund_top">
          <div styleName="AlreadyRefund_top_content">
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title">商户订单号</span>
              <span styleName="AlreadyRefund_top_item_content">
                L1254887956325545
              </span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title">保险总费用</span>
              <span styleName="AlreadyRefund_top_item_content">260元</span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title">订单状态</span>
              <span styleName="AlreadyRefund_top_item_content">已退款</span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title">姓名</span>
              <span styleName="AlreadyRefund_top_item_content">张金</span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title">身份证号</span>
              <span styleName="AlreadyRefund_top_item_content">
                2301558948954415
              </span>
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="AlreadyRefund_top_item">
              <span styleName="AlreadyRefund_top_item_title">手机号</span>
              <span styleName="AlreadyRefund_top_item_content">
                13945687412
              </span>
            </div>
          </div>
        </div>
        {this.state.insuranceList.map((v, i) => {
          return (
            <div styleName="AlreadyRefund_top1" key={i}>
              <div styleName="AlreadyRefund_top_item">
                <span styleName="AlreadyRefund_top_item_title1">
                  基本医疗保险续费
                </span>
                <Divider style={{ backgroundColor: "#B5B5B6" }} />
              </div>
              <div styleName="AlreadyRefund_top_item">
                <span styleName="AlreadyRefund_top_item_title">保险时间</span>
                <div styleName="AlreadyRefund_top_item_time">
                  <span styleName="AlreadyRefund_top_item_content1">
                    开始时间&nbsp;{v.time.fistTime}
                  </span>
                  <span styleName="AlreadyRefund_top_item_content1">
                    截止时间&nbsp;{v.time.lastTime}
                  </span>
                </div>
                <Divider style={{ backgroundColor: "#B5B5B6" }} />
              </div>
              <div styleName="AlreadyRefund_top_item">
                <span styleName="AlreadyRefund_top_item_title">保险总费用</span>
                <span styleName="AlreadyRefund_top_item_content">
                  {v.price}元
                </span>
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default CSSModules(AlreadyRefund, styles);



