import React from "react";
// import Carousel from "./carousel.js";
// import Dots from "./indicator-dots.js";
import SwipeableViews from 'react-swipeable-views';
import styles from "./index.css";
import CSSModules from "react-css-modules";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
// import { hashHistory } from "react-router-dom";

class ComponentsSwiper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      studentList: [
        {
          studentName: "王笑笑",
          idCardNumber: 23123654589631123,
          school: "哈尔滨音乐学院"
        },
        {
          studentName: "王笑笑",
          idCardNumber: 23123654589631123,
          school: "哈尔滨音乐学院"
        },
        {
          studentName: "王笑笑",
          idCardNumber: 23123654589631123,
          school: "哈尔滨音乐学院"
        }
      ]
    };
  }
  linkTo = () => {
    this.props.router.history.push("/MyOrder");
  };
  problem = () =>{
    this.props.router.history.push("/CommonProblem");
  }
  goToMedicalInsurance = () =>{
    this.props.router.history.push("/MedicalInsurance");
  }
  goToEdit = () => {
    this.props.router.history.push("/StudentInformationDetails");
  };
  addStudent = ()=>{
    this.props.router.history.push("/PrimaryStudentInformation");
  }
  changeSwiper = (index, type) =>{
    console.log(index)
  }
  componentDidMount() {
    console.log(this.props.type);
  }
  render() {
    return (
      <div styleName="ComponentsSwiper">
        {this.props.type === "Parent" && (
          // <Carousel axis={"x"} widgets={[Dots]} className="custom-class">
          <SwipeableViews onSwitching={this.changeSwiper} >
            {this.state.studentList.map((v, i) => {
              return (
                <div styleName="ComponentsSwiper_top" key={i}>
                  <div onClick={this.goToEdit}>
                    <div styleName="triangle-topright" />
                    <span
                      className="iconfont icon-shiliangzhinengduixiang"
                      styleName="editIcon"
                    />
                  </div>
                  <div styleName="ComponentsSwiper_top_content">
                    <div styleName="ComponentsSwiper_top_item">
                      <span styleName="ComponentsSwiper_top_item_title">
                        学生姓名
                      </span>
                      <span styleName="ComponentsSwiper_top_item_content">
                        {v.studentName}
                      </span>
                      {/* <div styleName="ComponentsSwiper_top_item_line" /> */}
                      <Divider style={{ backgroundColor: "#B5B5B6" }} />
                    </div>
                    <div styleName="ComponentsSwiper_top_item">
                      <span styleName="ComponentsSwiper_top_item_title">
                        身份证号
                      </span>
                      <span styleName="ComponentsSwiper_top_item_content">
                        {v.idCardNumber}
                      </span>
                      {/* <div styleName="ComponentsSwiper_top_item_line" /> */}
                      <Divider style={{ backgroundColor: "#B5B5B6" }} />
                    </div>
                    <div styleName="ComponentsSwiper_top_item">
                      <span styleName="ComponentsSwiper_top_item_title">
                        所在学院
                      </span>
                      <span styleName="ComponentsSwiper_top_item_content">
                        {v.school}
                      </span>
                      <Divider style={{ backgroundColor: "#B5B5B6" }} />
                      <p
                        styleName="ComponentsSwiper_top_item_call"
                        onClick={this.props.openClassSalesman}
                      >
                        <span className="iconfont icon-dianhua" />
                        联系业务员
                      </p>
                    </div>
                  </div>
                </div>
              );
            })}
            {this.state.studentList.length < 5 && (
              <div styleName="ComponentsSwiper_top1">
                <div styleName="ComponentsSwiper_top_content1">
                  <span
                    className="iconfont icon-xinzeng"
                    styleName="ComponentsSwiper_top_content1_span"
                  />
                  添加学生
                </div>
              </div>
            )}
          </SwipeableViews>
          // </Carousel> 
        )}
        {this.props.type === "CollegeStudentHome" && (
          <p
            styleName="ComponentsSwiper_top_item_call1"
            onClick={this.props.openClassSalesman}
          >
            <span className="iconfont icon-weixin" style={{color:"#09AF22"}}/>
            联系业务员
          </p>
        )}
        <Button
          variant="contained"
          color="secondary"
          style={{ color: "#fff", textDecoration: "none" }}
          styleName="ComponentsSwiper_button"
          onClick={this.goToMedicalInsurance}
        >
          保险产品
        </Button>
        <div styleName="tips">
          注：您近期购买的"基础医疗保险" / "商业保险"截止日期为：2019-12-31 23:59:59。
        </div>
        <Button
          variant="contained"
          color="primary"
          style={{ color: "#fff" }}
          styleName="ComponentsSwiper_button1"
          onClick={this.linkTo}
        >
          <span
            className="iconfont icon-dingdantijiaochenggong"
            styleName="button_icon"
          />
          &nbsp;我的订单
        </Button>
        <Button
          variant="contained"
          color="primary"
          style={{ color: "#fff", marginLeft: "10px" }}
          styleName="ComponentsSwiper_button1"
          onClick={this.problem}
        >
          <span className="iconfont icon-wenhao" styleName="button_icon" />
          &nbsp;常见问题
        </Button>
        {/* <Button
          variant="contained"
          style={{ color: "#fff", backgroundColor: "#B5B5B6" }}
          styleName="ComponentsSwiper_button2"
          onClick={this.linkTo}
        >
          解绑
        </Button> */}
        <div styleName="ComponentsSwiper_adBox">
            <p styleName="ComponentsSwiper_ad">公告区</p>
            <span styleName="ComponentsSwiper_adSpan">
              哈尔滨理财促销,保险降价请查看哈尔滨理财促销,保险降价请查看哈尔滨理财促销,保险降价请查看哈尔滨理财促销,保险降价请查看哈尔滨理财促销。
            </span>
        </div>
      </div>
    );
  }
}

export default CSSModules(ComponentsSwiper, styles);



