import React, { Component } from "react";
import styles from "./index.css";
import CSSModules from "react-css-modules";
import Button from "@material-ui/core/Button";

class Agreement extends Component {
  constructor(props) {
    super(props);
    document.title = "协议";
    this.state = {};
  }

  render() {
    return (
      <div style={{ height: "100%" }}>
        <div styleName="Agreement">
          <div
            dangerouslySetInnerHTML={{ __html: "<div>这里是个富文本</div>" }}
          />
          <Button
            variant="contained"
            style={{
              color: "#fff",
              display: "block",
              margin: "0 auto"
            }}
            color="primary"
            styleName="Agreement_submitBox_button"
            onClick={() => {}}
          >
            已阅读
          </Button>
        </div>
        <div styleName="Agreement_block" />
      </div>
    );
  }
}

export default CSSModules(Agreement, styles);



