import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
// import MenuItem from '@material-ui/core/MenuItem';
import Divider from '@material-ui/core/Divider';
import Modal from '@material-ui/core/Modal';

class PrimaryStudentInformation extends Component {
  constructor(props) {
    super(props);
    document.title = '学生信息';
    this.state = {
      affiliatedInstitutions: '',
      studentName: '',
      idCardNo: '',
      grade: '',
      studentClass: '',
      nation: '',
      scholasticYearSystem: '',
      isCollegeStudent: true,
      AdultOrNot:true,
      open: true
    };
  }
  handleChange = name => event => {
    console.log(name);
    this.setState({
      [name]: event.target.value
    });
    let datas = JSON.parse(localStorage.getItem('StudentInformation_formData'));
    console.log(datas);
    if (datas === null) {
      datas = {};
      datas[name] = event.target.value;
      localStorage.setItem(
        'StudentInformation_formData',
        JSON.stringify(datas)
      );
    } else {
      datas[name] = event.target.value;
      localStorage.setItem(
        'StudentInformation_formData',
        JSON.stringify(datas)
      );
    }
  };

  linkTo = () => {
    this.props.history.push('/ParentHome');
  };
  getHelp = () => {
    this.setState({
      open: true
    });
  };
  closeModal = () => {
    this.setState({
      open: false
    });
  };
  UniversityChoice = () => {
    this.props.history.push({
      pathname: '/UniversityChoice',
      pageName: 'PrimaryStudentInformation'
    });
  };
  componentDidMount() {
    let datas = localStorage.getItem('StudentInformation_formData');
    if (datas !== null) {
      let formDatas = JSON.parse(datas);
      this.setState(formDatas);
      // localStorage.removeItem('StudentInformation_formData');
    }
    console.log(localStorage.getItem('PrimaryStudentInformation_open'))
    if(localStorage.getItem('PrimaryStudentInformation_open')){
      this.setState({
        open : false
      })
    }else{
      this.setState({
        open : true
      })
      localStorage.setItem('PrimaryStudentInformation_open',true)
    }
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="PrimaryStudentInformation">
        <div styleName="PrimaryStudentInformation_top">
          <div styleName="PrimaryStudentInformation_top_content">
          <div styleName="PrimaryStudentInformation_top_item">
              <span styleName="PrimaryStudentInformation_top_item_title">
                家长姓名
              </span>
              <span styleName="PrimaryStudentInformation_top_item_content">
                王昌龄
              </span>
              {/* <div styleName="PrimaryStudentInformation_top_item_line" /> */}
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="PrimaryStudentInformation_top_item">
              <span styleName="PrimaryStudentInformation_top_item_title">
                身份证号
              </span>
              <span styleName="PrimaryStudentInformation_top_item_content">
                230198756213215865
              </span>
              {/* <div styleName="PrimaryStudentInformation_top_item_line" /> */}
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="PrimaryStudentInformation_top_item">
              <span styleName="PrimaryStudentInformation_top_item_title">
                联系电话
              </span>
              <span styleName="PrimaryStudentInformation_top_item_content1">
                13265894587
              </span>
            </div>
          </div>
        </div>
        <div styleName="PrimaryStudentInformation_info">
          <form
            style={{ marginTop: window.screen.height <= 568 ? '' : '20px' }}
            styleName="PrimaryStudentInformation_from"
            autoComplete="off"
          >
           <TextField
              label={
                <span
                  className="iconfont icon-shiliangzhinengduixiang"
                  style={{ fontSize: '21px' }}
                >
                  <span style={{ fontSize: '21px' }}>学生姓名</span>
                </span>
              }
              value={this.state.studentName}
              styleName="PrimaryStudentInformation_input"
              placeholder="请输入学生姓名"
              InputProps={{ style: { width: 'inherit', height: 'inherit',paddingTop:"0.5rem" } }}
              InputLabelProps={{ shrink: true }}
              onChange={this.handleChange('studentName')}
              margin="normal"
              variant="outlined"
            />
            <TextField
              label={
                <span
                  className="iconfont icon-shiliangzhinengduixiang"
                  style={{ fontSize: '21px' }}
                >
                  <span style={{ fontSize: '21px' }}>身份证号</span>
                </span>
              }
              value={this.state.idCardNo}
              styleName="PrimaryStudentInformation_input"
              placeholder="请输入身份证号"
              InputProps={{ style: { width: 'inherit', height: 'inherit',paddingTop:"0.5rem" } }}
              InputLabelProps={{ shrink: true }}
              onChange={this.handleChange('idCardNo')}
              margin="normal"
              variant="outlined"
            />
            <TextField
              select
              label={
                <span
                  className="iconfont icon-shiliangzhinengduixiang"
                  style={{ fontSize: '21px' }}
                >
                  <span style={{ fontSize: '21px' }}>民族</span>
                </span>
              }
              value={this.state.nation}
              styleName="PrimaryStudentInformation_select"
              onChange={this.handleChange('nation')}
              SelectProps={{
                native:true,
                style: { width: 'inherit', height: 'inherit',paddingTop:"0.5rem" }
              }}
              InputLabelProps={{ shrink: true }}
              margin="normal"
              variant="outlined"
            >
              <option key={'汉族'} value={'汉族'}>
                {'汉族'}
              </option>
              <option key={'满族'} value={'满族'}>
                {'满族'}
              </option>
            </TextField>
            {/*<TextField
              select
              label={
                <span
                  className="iconfont icon-shiliangzhinengduixiang"
                  style={{ fontSize: '21px' }}
                >
                  <span style={{ fontSize: '21px' }}>所属院校</span>
                </span>
              }
              value={this.state.affiliatedInstitutions}
              styleName="PrimaryStudentInformation_select"
              onChange={this.handleChange('affiliatedInstitutions')}
              SelectProps={{
                style: { width: 'inherit', height: 'inherit' }
              }}
              InputLabelProps={{ shrink: true }}
              margin="normal"
              variant="outlined"
            >
              <MenuItem key={'哈尔滨工业大学'} value={'哈尔滨工业大学'}>
                {'哈尔滨工业大学'}
              </MenuItem>
              <MenuItem key={'黑龙江大学'} value={'黑龙江大学'}>
                {'黑龙江大学'}
              </MenuItem>
            </TextField>*/}
            <div
              styleName="PrimaryStudentInformation_affiliatedInstitutionsBox"
              onClick={this.UniversityChoice}
            >
              <TextField
                label={
                  <span
                    className="iconfont icon-shiliangzhinengduixiang"
                    style={{ fontSize: '21px' }}
                  >
                    <span style={{ fontSize: '21px' }}>所属学校</span>
                  </span>
                }
                value={this.state.affiliatedInstitutions}
                styleName="PrimaryStudentInformation_input"
                InputProps={{
                  style: { width: 'inherit', height: 'inherit',paddingTop:"0.5rem" },
                  readOnly: true
                }}
                InputLabelProps={{ shrink: true }}
                onChange={this.handleChange('affiliatedInstitutions')}
                margin="normal"
                variant="outlined"
              />
              <div styleName="triangle-down" />
            </div>
            <TextField
              label={
                <span
                  className="iconfont icon-shiliangzhinengduixiang"
                  style={{ fontSize: '21px' }}
                >
                  <span style={{ fontSize: '21px' }}>年级</span>
                </span>
              }
              value={this.state.grade}
              styleName="PrimaryStudentInformation_input"
              placeholder="请输入年级"
              InputProps={{ style: { width: 'inherit', height: 'inherit',paddingTop:"0.5rem" } }}
              InputLabelProps={{ shrink: true }}
              onChange={this.handleChange('grade')}
              margin="normal"
              variant="outlined"
            />
            <TextField
              label={
                <span
                  className="iconfont icon-shiliangzhinengduixiang"
                  style={{ fontSize: '21px' }}
                >
                  <span style={{ fontSize: '21px' }}>班级</span>
                </span>
              }
              value={this.state.studentClass}
              styleName="PrimaryStudentInformation_input"
              placeholder="请输入班级"
              InputProps={{ style: { width: 'inherit', height: 'inherit',paddingTop:"0.5rem" } }}
              InputLabelProps={{ shrink: true }}
              onChange={this.handleChange('studentClass')}
              margin="normal"
              variant="outlined"
            />
            <Button
              variant="contained"
              color="primary"
              style={{ marginTop: '10px', marginBottom: '20px' }}
              styleName="PrimaryStudentInformation_button"
              onClick={this.linkTo}
            >
              绑定
            </Button>
          </form>
        </div>
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.open}
          styleName="PrimaryStudentInformation_modal"
        >
          <div styleName="PrimaryStudentInformation_modalDiv">
            <p styleName="PrimaryStudentInformation_modalDiv_p">提示</p>
            <p styleName="PrimaryStudentInformation_modalDiv_span">
              一个家长最多可以绑定
            </p>
            <p styleName="PrimaryStudentInformation_modalDiv_span">
              5个学生信息
            </p>
            <Button
              variant="contained"
              color="primary"
              style={{ borderRadius: '65px' }}
              styleName="PrimaryStudentInformation_modalButton"
              onClick={this.closeModal}
            >
              知道了
            </Button>
          </div>
        </Modal>
      </div>
    );
  }
}

export default CSSModules(PrimaryStudentInformation, styles, {
  allowMultiple: true
});



