import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Divider from '@material-ui/core/Divider';
import Input from '@material-ui/core/Input';

class StudentInformationDetails extends Component {
  constructor(props) {
    super(props);
    document.title = '学生信息';
    this.state = {
      studentName: '周星星',
      idCardNumber: '23060612364587',
      affiliatedInstitutions: '哈尔滨音乐学院',
      grade: '三年级',
      studentClass: '7班',
      nation: '汉族',
      scholasticYearSystem: '三年制',
      isCollegeStudent: true,
      isEdit: true
    };
  }
  // handleChange = name => event => {
  //   console.log(name);
  //   this.setState({
  //     [name]: event.target.value
  //   });
  // };
  linkTo = () => {
    this.props.history.push('/App');
  };
  changeIpnut = (event, name) => {
    if (name === 'studentName') {
      this.setState({ studentName: event.target.value });
    } else if (name === 'idCardNumber') {
      this.setState({ idCardNumber: event.target.value });
    } else if (name === 'grade') {
      this.setState({ grade: event.target.value });
    } else if (name === 'studentClass') {
      this.setState({ studentClass: event.target.value });
    } else if (name === 'nation') {
      this.setState({ nation: event.target.value });
    } else if (name === 'scholasticYearSystem') {
      this.setState({ scholasticYearSystem: event.target.value });
    }
  };
  UniversityChoice = () => {
    if (!this.state.isEdit) {
      let datas = JSON.parse(
        localStorage.getItem('StudentInformationDetails_formData')
      );
      console.log(datas);
      if (datas === null) {
        let dataKeysList = [
          'studentName',
          'idCardNumber',
          'grade',
          'studentClass',
          'scholasticYearSystem',
          'nation',
          'isEdit'
        ];
        datas = {};
        let _this = this;
        dataKeysList.map((v, i) => {
          datas[v] = _this.state[v];
          return false;
        });
        localStorage.setItem(
          'StudentInformationDetails_formData',
          JSON.stringify(datas)
        );
      }
      this.props.history.push({
        pathname: '/UniversityChoice',
        pageName: 'StudentInformationDetails'
      });
    }
  };
  editorread = () => {
    this.setState({
      isEdit: !this.state.isEdit,
      isCollegeStudent: !this.state.isCollegeStudent
    });
    if (this.state.isEdit === true) {
      // 获取焦点
      let inputFocus = document.getElementById(
        'StudentInformationDetails_firstInput'
      );
      inputFocus.focus();
    }
  };
  componentDidMount() {
    let datas = localStorage.getItem('StudentInformationDetails_formData');
    if (datas !== null) {
      let formDatas = JSON.parse(datas);
      console.log(formDatas);
      this.setState(formDatas);
      localStorage.removeItem('StudentInformationDetails_formData');
    }
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="StudentInformationDetails">
        <div styleName="StudentInformationDetails_top">
          <div onClick={this.editorread}>
            <div
              styleName="triangle-topright"
              style={{
                borderColor: this.state.isEdit
                  ? '#009B63 transparent transparent #009B63'
                  : '#F39800 transparent transparent #F39800'
              }}
            />
            {this.state.isEdit ? (
              <span
                className="iconfont icon-shiliangzhinengduixiang"
                styleName="editIcon"
              />
            ) : (
              <span className="iconfont icon-save" styleName="editIcon" />
            )}
          </div>
          <div styleName="StudentInformationDetails_top_content">
            <div styleName="StudentInformationDetails_top_item">
              <span styleName="StudentInformationDetails_top_item_title">
                学生姓名
              </span>

              <Input
                readOnly={this.state.isEdit}
                value={this.state.studentName}
                id="StudentInformationDetails_firstInput"
                styleName="StudentInformationDetails_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'studentName');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="StudentInformationDetails_top_item">
              <span styleName="StudentInformationDetails_top_item_title">
                身份证号
              </span>
              <Input
                readOnly={this.state.isEdit}
                value={this.state.idCardNumber}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                styleName="StudentInformationDetails_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'idCardNumber');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div
              styleName="StudentInformationDetails_top_item"
              onClick={this.UniversityChoice}
            >
              <span styleName="StudentInformationDetails_top_item_title">
                所在院校
              </span>
              {/*<Input
                readOnly={true}
                onClick={this.UniversityChoice}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                value={this.state.affiliatedInstitutions}
                styleName="StudentInformationDetails_input"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'affiliatedInstitutions');
                }}
              />*/}
              <span
                styleName="StudentInformationDetails_top_item_content"
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
              >
                {this.state.affiliatedInstitutions}
              </span>
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="StudentInformationDetails_top_item">
              <span styleName="StudentInformationDetails_top_item_title">
                年级
              </span>
              <Input
                readOnly={this.state.isEdit}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                value={this.state.grade}
                styleName="StudentInformationDetails_input1"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'grade');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="StudentInformationDetails_top_item">
              <span styleName="StudentInformationDetails_top_item_title">
                班级
              </span>
              <Input
                readOnly={this.state.isEdit}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                value={this.state.studentClass}
                styleName="StudentInformationDetails_input1"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'studentClass');
                }}
              />
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="StudentInformationDetails_top_item">
              <span styleName="StudentInformationDetails_top_item_title">
                民族
              </span>
              <Input
                readOnly={this.state.isEdit}
                style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                value={this.state.nation}
                styleName="StudentInformationDetails_input1"
                inputProps={{
                  'aria-label': 'Description'
                }}
                disableUnderline={true}
                onChange={e => {
                  this.changeIpnut(e, 'nation');
                }}
              />
              {this.state.isCollegeStudent && (
                <Divider style={{ backgroundColor: '#B5B5B6' }} />
              )}
            </div>
            {this.state.isCollegeStudent && (
              <div styleName="StudentInformationDetails_top_item">
                <span styleName="StudentInformationDetails_top_item_title">
                  学年制
                </span>
                <Input
                  readOnly={this.state.isEdit}
                  style={{ color: this.state.isEdit ? '#000' : '#B5B5B6' }}
                  value={this.state.scholasticYearSystem}
                  styleName="StudentInformationDetails_input2"
                  inputProps={{
                    'aria-label': 'Description'
                  }}
                  disableUnderline={true}
                  onChange={e => {
                    this.changeIpnut(e, 'scholasticYearSystem');
                  }}
                />
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default CSSModules(StudentInformationDetails, styles);



