import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
// import MenuItem from '@material-ui/core/MenuItem';
import Modal from '@material-ui/core/Modal';
import imgUrl from '../images/logo.png';

class ChoosingRoles extends Component {
  constructor(props) {
    super(props);
    document.title = '注册';
    this.state = {
      roles: '大学生',
      open: false
    };
  }
  handleChange = name => event => {
    console.log(name);
    this.setState({
      [name]: event.target.value
    });
  };
  linkTo = () => {
    if(this.state.roles === '大学生'){
      this.props.history.push('/Register');
    }else if(this.state.roles === '中小学生'){
      this.props.history.push('/PrimaryStudentRegister');
    }
  };
  getHelp = () => {
    this.setState({
      open: true
    });
  };
  closeModal = () => {
    this.setState({
      open: false
    });
  };
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="loginBox">
        <div styleName="loginBox_top">
          <img width="100%" height="100%" src={imgUrl} alt="logo" />
        </div>
        <div styleName="loginBox_info">
          <form
            style={{ marginTop: '50px' }}
            styleName="loginBox_from"
            autoComplete="off"
          >
            <TextField
              select
              label={
                <span
                  className="iconfont icon-shiliangzhinengduixiang"
                  style={{ fontSize: '21px' }}
                >
                  <span style={{ fontSize: '21px' }}>用户身份</span>
                </span>
              }
              value={this.state.roles}
              styleName="loginBox_select"
              onChange={this.handleChange('roles')}
              SelectProps={{
                native:true,
                style: { width: 'inherit', height: 'inherit',paddingTop:"0.5rem" }
              }}
              InputLabelProps={{ shrink: true }}
              margin="normal"
              variant="outlined"
            >
              <option key={'大学生'} value={'大学生'}>
                {'大学生'}
              </option>
              <option key={'中小学生'} value={'中小学生'}>
                {'中小学生'}
              </option>
            </TextField>
            <Button
              variant="contained"
              color="primary"
              style={{ marginTop: '20px' }}
              styleName="loginBox_button"
              onClick={this.linkTo}
            >
              下一步
            </Button>
          </form>
          <div styleName="loginBox_adBox">
            <p styleName="loginBox_ad">公告区</p>
            <span styleName="loginBox_adSpan">
              哈尔滨理财促销,保险降价请查看哈尔滨理财促销,保险降价请查看哈尔滨理财促销,保险降价请查看哈尔滨理财促销,保险降价请查看哈尔滨理财促销。
            </span>
          </div>
        </div>
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.open}
          styleName="loginBox_modal"
        >
          <div styleName="loginBox_modalDiv">
            <p styleName="loginBox_modalDiv_p">提示</p>
            <p styleName="loginBox_modalDiv_span">
              年满18周岁用户以大学生身份进行投保,未满18周岁用户家长可使用家长身份证号代替登录进行投保操作。
            </p>
            <Button
              variant="contained"
              color="primary"
              style={{ borderRadius: '65px' }}
              styleName="loginBox_modalButton"
              onClick={this.closeModal}
            >
              知道了
            </Button>
          </div>
        </Modal>
      </div>
    );
  }
}

export default CSSModules(ChoosingRoles, styles);



