import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';

class OrderCompleted extends Component {
  constructor(props) {
    super(props);
    document.title = '订单详情';
    this.state = {
      insuranceList: [
        {
          time: {
            fistTime: '2019-08-08 08:00:00',
            lastTime: '2019-08-08 08:00:00'
          },
          status: 1,
          price: 260
        },
        {
          time: {
            fistTime: '2019-08-08 08:00:00',
            lastTime: '2019-08-08 08:00:00'
          },
          status: 2,
          price: 260
        }
      ]
    };
  }
  callSalesman = () => {};
  showDetails = () =>{

  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="OrderCompleted">
        <div styleName="OrderCompleted_top">
          <div styleName="OrderCompleted_top_content">
            <div styleName="OrderCompleted_top_item">
              <span styleName="OrderCompleted_top_item_title">商户订单号</span>
              <span styleName="OrderCompleted_top_item_content">
                L1254887956325545
              </span>
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="OrderCompleted_top_item">
              <span styleName="OrderCompleted_top_item_title">支付金额</span>
              <span styleName="OrderCompleted_top_item_content">260元</span>
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="OrderCompleted_top_item">
              <span styleName="OrderCompleted_top_item_title">订单状态</span>
              <span styleName="OrderCompleted_top_item_content">已完成</span>
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="OrderCompleted_top_item">
              <span styleName="OrderCompleted_top_item_title">姓名</span>
              <span styleName="OrderCompleted_top_item_content">张金</span>
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="OrderCompleted_top_item">
              <span styleName="OrderCompleted_top_item_title">身份证号</span>
              <span styleName="OrderCompleted_top_item_content">
                2301558948954415
              </span>
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="OrderCompleted_top_item">
              <span styleName="OrderCompleted_top_item_title">手机号</span>
              <span styleName="OrderCompleted_top_item_content">
                13945687412
              </span>
            </div>
          </div>
        </div>
        <div styleName="bottonBox">
          <Button
            variant="contained"
            color="primary"
            styleName="OrderCompleted_Refundbutton"
            onClick={this.callSalesman}
          >
            联系业务员
          </Button>
        </div>
        {this.state.insuranceList.map((v, i) => {
          return (
            <div styleName="OrderCompleted_top1" key={i}>
              <div styleName="OrderCompleted_top_item">
                <span styleName="OrderCompleted_top_item_title1">
                  基本医疗保险续费
                </span>
                  <div styleName="OrderCompleted_top_item_title1_tag" onClick={this.showDetails}>
                  查看详情
                    <span
                      className="iconfont icon-xiangyoujiantou"
                      style={{fontSize:"12px",fontWeight:700,color:"#B5B5B6"}}
                    ></span>
                  </div>
                <Divider style={{ backgroundColor: '#B5B5B6' }} />
              </div>
              <div styleName="OrderCompleted_top_item">
                <span styleName="OrderCompleted_top_item_title">保险时间</span>
                <div styleName="OrderCompleted_top_item_time">
                  <span styleName="OrderCompleted_top_item_content1">
                    开始时间&nbsp;{v.time.fistTime}
                  </span>
                  <span styleName="OrderCompleted_top_item_content1">
                    截止时间&nbsp;{v.time.lastTime}
                  </span>
                </div>
                <Divider style={{ backgroundColor: '#B5B5B6' }} />
              </div>
              <div styleName="OrderCompleted_top_item">
                <span styleName="OrderCompleted_top_item_title">
                  保险总费用
                </span>
                <span styleName="OrderCompleted_top_item_content">
                  {v.price}元
                </span>
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default CSSModules(OrderCompleted, styles);



