import React, { Component } from "react";
import styles from "./index.css";
import CSSModules from "react-css-modules";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Divider from "@material-ui/core/Divider";

class ConfirmOrder extends Component {
  constructor(props) {
    super(props);
    document.title = "确认订单";
    this.state = {
      paymentCode: "",
      isInputCode:false,
      InsuranceList: [
        {
          title: "城乡居民基本医疗保险",
          time: {
            firstTime: "2019-09-09 08:00:00",
            lastTime: "2019-09-09 08:00:00"
          },
          tips: "大学生首次参保9月1日开始",
          Cost: 300
        },
        {
          title: "学生儿童补充医疗保险(短期)",
          time: {
            firstTime: "2019-09-09 08:00:00",
            lastTime: "2019-09-09 08:00:00"
          },
          Cost: 300,
        }
      ]
    };
  }
  handleChange = name => event => {
    if(name === 'paymentCode'){
      if(event.target.value === '' || event.target.value == null){
        this.setState({
          [name]: event.target.value,
          isInputCode:false
        });
      }else{
        this.setState({
          [name]: event.target.value,
          isInputCode:true
        });
      }
    }
  };
  linkTo = () => {
    this.props.history.push("/App");
  };
  getHelp = () => {
    this.setState({
      open: true
    });
  };
  closeModal = () => {
    this.setState({
      open: false
    });
  };
  pay = () =>{
    if(this.state.isInputCode === false){
      return false;
    }
    // this.props.history.push("/ConfirmOrder")
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="ConfirmOrder">
        <div styleName="ConfirmOrder_top">
          <div>
            <div styleName="ConfirmOrder_top_item">
              <span styleName="ConfirmOrder_top_item_title">学生姓名</span>
              <span styleName="ConfirmOrder_top_item_content">周晓晓</span>
              {/* <div styleName="ConfirmOrder_top_item_line" /> */}
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="ConfirmOrder_top_item">
              <span styleName="ConfirmOrder_top_item_title">身份证号</span>
              <span styleName="ConfirmOrder_top_item_content">
                230198756213215865
              </span>
              {/* <div styleName="ConfirmOrder_top_item_line" /> */}
              <Divider style={{ backgroundColor: "#B5B5B6" }} />
            </div>
            <div styleName="ConfirmOrder_top_item">
              <span styleName="ConfirmOrder_top_item_title">联系电话</span>
              <span styleName="ConfirmOrder_top_item_content">13265894587</span>
            </div>
          </div>
        </div>
        
        {this.state.InsuranceList.map((v, i) => {
          return (
            <div styleName="ConfirmOrder_content" key={i}>
              <p styleName="ConfirmOrder_content_item_p">
                <span styleName="ConfirmOrder_content_item_span">
                  保险名称&nbsp;&nbsp;
                </span>
                {v.title}
              </p>
              <p styleName="ConfirmOrder_content_item_p">
                <span styleName="ConfirmOrder_content_item_span">
                  开始时间&nbsp;&nbsp;
                </span>
                {v.time.firstTime}
              </p>
              <p styleName="ConfirmOrder_content_item_p">
                <span styleName="ConfirmOrder_content_item_span">
                  截止时间&nbsp;&nbsp;
                </span>
                {v.time.lastTime}
              </p>
              <p styleName="ConfirmOrder_content_item_p">
                <span styleName="ConfirmOrder_content_item_span">
                  购买金额&nbsp;&nbsp;
                </span>
                ¥{v.Cost}
              </p>
              {/* <div styleName="ConfirmOrder_infoBox">
                <p styleName="ConfirmOrder_infoBox_title">{v.title}</p>
                <div styleName="ConfirmOrder_infoBox_timeSpan">
                  <div styleName="ConfirmOrder_infoBox_timeSpan_line" />
                  保险期间
                  <div styleName="ConfirmOrder_infoBox_timeSpan_line1" />
                </div>
                <div styleName="ConfirmOrder_infoBox_time">
                  {v.time.firstTime} 至 <span>{v.time.lastTime}</span>
                </div>
                <div styleName="ConfirmOrder_infoBox_timeSpan">
                  <div styleName="ConfirmOrder_infoBox_timeSpan_line" />
                  保险费用
                  <div styleName="ConfirmOrder_infoBox_timeSpan_line1" />
                </div>
                <div styleName="ConfirmOrder_infoBox_Cost">
                  ¥
                  <span styleName="ConfirmOrder_infoBox_CostSpan">
                    {v.Cost}
                  </span>
                  .00元
                </div>
              </div> */}
            
            </div>
          );
        })}
        <div styleName="ConfirmOrder_submitBox">
          <div styleName="ConfirmOrder_priceBox">
            <span>总金额</span>
            <div styleName="ConfirmOrder_priceBox_number">
              ¥&nbsp;
              <span styleName="ConfirmOrder_priceBox_numberSpan">600</span>.00元
            </div>
          </div>
          <TextField
            label={
              <span
                className="iconfont icon-shiliangzhinengduixiang"
                style={{ fontSize: "21px" }}
              >
                <span style={{ fontSize: "21px" }}>付款码</span>
              </span>
            }
            value={this.state.paymentCode}
            styleName="ConfirmOrder_input"
            placeholder="请输入付款码"
            InputProps={{ style: { width: "inherit", height: "inherit" ,paddingTop:"0.5rem"} }}
            InputLabelProps={{ shrink: true }}
            onChange={this.handleChange("paymentCode")}
            margin="normal"
            variant="outlined"
          />
          <Button
            variant="contained"
            color="primary"
            style={{
              color: "#fff",
              textDecoration: "none",
              backgroundColor: this.state.isInputCode
              ? '#009B63'
              : '#B5B5B6'
            }}
            styleName="ConfirmOrder_submitBox_button"
            onClick={this.pay}
          >
            立即支付
          </Button>
        </div>
      </div>
    );
  }
}

export default CSSModules(ConfirmOrder, styles);



