import React, { Component } from "react";
import styles from "./index.css";
import CSSModules from "react-css-modules";

class TabsContent extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  goToPay = () => {};
  render() {
    // const TabsContent = datas => {
    //   console.log(datas);
    //   return (
    //     <div styleName="TabsContent_content_box">
    //       <div styleName="TabsContent_content_item" />
    //     </div>
    //   );
    return (
      <div styleName="TabsContent_content_box">
        {this.props.orderList.map((v, i) => {
          return (
            <div styleName="TabsContent_content_item" key={i}>
              <p styleName="TabsContent_content_item_p">
                <span styleName="TabsContent_content_item_span">
                  商户订单&nbsp;&nbsp;
                </span>
                {v.orderNumber}
              </p>
              <p styleName="TabsContent_content_item_p">
                <span styleName="TabsContent_content_item_span">
                  购买项目&nbsp;&nbsp;
                </span>
                {v.projectName}
              </p>
              <p styleName="TabsContent_content_item_p">
                <span styleName="TabsContent_content_item_span">
                  购买金额&nbsp;&nbsp;
                </span>
                ¥{v.price}
              </p>
              {v.stauts === 1 && (
                <span styleName="TabsContent_content_item_status">待支付</span>
              )}
              <div styleName="TabsContent_content_Cancelbutton">取消订单</div>
              {v.stauts === 1 && (
                <div styleName="TabsContent_content_Paybutton">去支付</div>
              )}
            </div>
          );
        })}
      </div>
    );
  }
}

CSSModules(TabsContent, styles);

export default CSSModules(TabsContent, styles);



