import React from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Input from '@material-ui/core/Input';

class UniversityChoice extends React.Component {
  constructor(props) {
    super(props);
    document.title = '选择所属院校';
    this.state = {
      keyword: '',
      toastObj: '', //弹窗对象
      timeout: '',
      timeout1: '',
      letter: ['A', 'B', 'C', 'D', 'E'],
      schoolName: [
        { key: 'A', content: ['阿里巴巴商学院', '爱丽斯卡大学', '阿富汗大学'] },
        { key: 'B', content: ['北京大学', '北京交通大学', '北京电影学院'] },
        { key: 'C', content: ['重庆大学', '重庆医科大学', '重庆交通学院'] },
        { key: 'D', content: ['大庆石油学院', '大庆卫生学校', '大庆公安学院'] },
        { key: 'E', content: ['洱海大学', '洱海交通大学', '洱海职业技术学院'] }
      ]
    };
  }
  scrollToAnchor = anchorName => {
    this.Toast(anchorName, 500);
    if (anchorName) {
      let anchorElement = document.getElementById(anchorName);
      if (anchorElement) {
        anchorElement.scrollIntoView(true);
      }
    }
  };
  selectSchool = name => {
    if (this.props.location.pageName === 'PrimaryStudentInformation' ||this.props.location.pageName === 'AdvancedStudentInformation') {
      let fromData;
      if (localStorage.getItem('StudentInformation_formData') === null) {
        fromData = {};
      } else {
        fromData = JSON.parse(
          localStorage.getItem('StudentInformation_formData')
        );
      }
      fromData['affiliatedInstitutions'] = name;
      localStorage.setItem(
        'StudentInformation_formData',
        JSON.stringify(fromData)
      );
    } else if (this.props.location.pageName === 'StudentInformationDetails') {
      let fromData = JSON.parse(
        localStorage.getItem('StudentInformationDetails_formData')
      );
      fromData['affiliatedInstitutions'] = name;
      localStorage.setItem(
        'StudentInformationDetails_formData',
        JSON.stringify(fromData)
      );
    }
    this.props.history.goBack();
  };
  Toast = (msg, duration) => {
    duration = isNaN(duration) ? 3000 : duration;
    if (this.state.toastObj !== '') {
      clearTimeout(this.state.timeout);
      clearTimeout(this.state.timeout1);
      document.body.removeChild(this.state.toastObj);
      this.setState({ toastObj: '' });
    }
    var m = document.createElement('div');
    this.setState({ toastObj: m });
    m.innerHTML = msg;
    m.style.cssText =
      'width: 32%;opacity: 0.5;height: 110px;line-height:110px;color: rgb(255, 255, 255);text-align: center;border-radius: 10px;position: fixed;top: 35%;left: 35%;z-index: 999999;background: rgb(0, 0, 0);font-size: 32px;';
    document.body.appendChild(m);
    let timeout = setTimeout(() => {
      var d = 0.5;
      m.style.webkitTransition =
        '-webkit-transform ' + d + 's ease-in, opacity ' + d + 's ease-in';
      m.style.opacity = '0';
    }, duration);
    let timeout1 = setTimeout(() => {
      document.body.removeChild(m);
      this.setState({ toastObj: '' });
    }, 0.5 * 1000); //0.5这个值来自于前面的d
    this.setState({ timeout: timeout, timeout1: timeout1 });
  };
  changeIpnut = event => {
    this.setState({ keyword: event.target.value });
  };
  render() {
    return (
      <div styleName="selectBox">
        <div styleName="selectBox_inputBox">
          <span
            className="iconfont icon-sousuo"
            styleName="selectBox_inputBox_icon"
          />
          <Input
            style={{ color: '#000' }}
            value={this.state.keyword}
            styleName="selectBox_input"
            inputProps={{
              'aria-label': 'Description'
            }}
            placeholder="请输入学校名称"
            disableUnderline={true}
            onChange={e => {
              this.changeIpnut(e, 'studentClass');
            }}
          />
        </div>
        {this.state.schoolName.map((v, i) => {
          return (
            <div styleName="selectBox_itemBox" id={v.key} key={i}>
              <p styleName="selectBox_letter">{v.key}</p>
              {v.content.map((value, index) => {
                return (
                  <div
                    styleName="selectBox_item"
                    key={index}
                    onClick={() => {
                      this.selectSchool(value);
                    }}
                  >
                    {value}
                  </div>
                );
              })}
            </div>
          );
        })}
        <div styleName="letterBotton">
          {this.state.letter.map((v, i) => {
            return (
              <div
                styleName="letterBotton_text"
                id={v}
                key={v}
                onClick={() => {
                  this.scrollToAnchor(v);
                }}
              >
                {v}
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

// export default UniversityChoice;
export default CSSModules(UniversityChoice, styles);



