import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// import CSSModules from "react-css-modules";

class Test extends Component {
  constructor(props) {
    super(props);
    document.title = '测试';
    this.state = {};
  }
  render() {
    return (
      <ul>
        <li>
          <Link to="/UniversityChoice">学校选择页面</Link>
        </li>
        <li>
          <Link to="/Register">大学生注册</Link>
        </li>
        <li>
          <Link to="/StudentInformationDetails">中小学生详情</Link>
        </li>
        <li>
          <Link to="/PrimaryStudentInformation">中小学生表单</Link>
        </li>
        <li>
          <Link to="/PaySuccess">支付成功</Link>
        </li>
        <li>
          <Link to="/ParentInformation">家长信息</Link>
        </li>
        <li>
          <Link to="/ParentHome">家长首页</Link>
        </li>
        <li>
          <Link to="/OrderDetails">待支付</Link>
        </li>
        <li>
          <Link to="/MyOrder">我的订单</Link>
        </li>
        <li>
          <Link to="/MedicalInsurance">保险产品</Link>
        </li>
        <li>
          <Link to="/ConfirmOrder">确认订单</Link>
        </li>
        <li>
          <Link to="/CommonProblem">常见问题</Link>
        </li>
        <li>
          <Link to="/CollegeStudentHome">大学生首页</Link>
        </li>
        <li>
          <Link to="/ApplyRefund">退款页面</Link>
        </li>
        <li>
          <Link to="/AlreadyRefund">已退款页面</Link>
        </li>
        <li>
          <Link to="/Agreement">协议页面</Link>
        </li>
        <li>
          <Link to="/AdvancedStudentInformation">
            大学生表单页面
          </Link>
        </li>
        <li>
          <Link to="/OrderCompleted">订单完成页面</Link>
        </li>
        <li>
          <Link to="/ChoosingRoles">选择身份页面</Link>
        </li>
        <li>
          <Link to="/PrimaryStudentRegister">中小学生注册页面</Link>
        </li>
      </ul>
    );
  }
}

export default Test;



