import Register from '../Register'; //注册页面
import PrimaryStudentInformation from '../PrimaryStudentInformation'; //中小学生信息页面
import AdvancedStudentInformation from '../AdvancedStudentInformation'; //大学生信息页面
import ParentInformation from '../ParentInformation'; //家长信息页面
import ParentHome from '../ParentHome'; //家长首页
import CollegeStudentHome from '../CollegeStudentHome'; //大学生首页
import StudentInformationDetails from '../StudentInformationDetails'; //学生信息详情页面
import MedicalInsurance from '../MedicalInsurance'; //基础医疗保险页面
import ConfirmOrder from '../ConfirmOrder'; //确认订单页面
import MyOrder from '../MyOrder'; //我的订单页面
import AlreadyRefund from '../AlreadyRefund'; //已退款页面
import OrderDetails from '../OrderDetails'; //待支付页面
import ApplyRefund from '../ApplyRefund'; //退款申请页面
import CommonProblem from '../CommonProblem'; //常见问题页面
import PaySuccess from '../PaySuccess'; //支付成功页面
import Agreement from '../Agreement'; //协议页面
import OrderCompleted from '../OrderCompleted'; //完成页面
import UniversityChoice from '../Components/UniversityChoice'; //大学选择页面
import ChoosingRoles from "../ChoosingRoles"//选择用户身份页面
import PrimaryStudentRegister from "../PrimaryStudentRegister"
import Test from '../Test'; //测试页面
// import AuthRouter from "../AuthRouter"; //权限组件
// import App from "../App";

export default [
  { path: '/', name: 'Test', component: Test, auth: true },
  { path: '/Register', name: 'Register', component: Register },
  {
    path: '/PrimaryStudentInformation',
    name: 'PrimaryStudentInformation',
    component: PrimaryStudentInformation,
    auth: true
  },
  {
    path: '/AdvancedStudentInformation',
    name: 'AdvancedStudentInformation',
    component: AdvancedStudentInformation,
    auth: true
  },
  {
    path: '/ParentInformation',
    name: 'ParentInformation',
    component: ParentInformation,
    auth: true
  },
  {
    path: '/ParentHome',
    name: 'ParentHome',
    component: ParentHome,
    auth: true
  },
  {
    path: '/CollegeStudentHome',
    name: 'CollegeStudentHome',
    component: CollegeStudentHome,
    auth: true
  },
  {
    path: '/StudentInformationDetails',
    name: 'StudentInformationDetails',
    component: StudentInformationDetails,
    auth: true
  },
  {
    path: '/MedicalInsurance',
    name: 'MedicalInsurance',
    component: MedicalInsurance,
    auth: true
  },
  {
    path: '/ConfirmOrder',
    name: 'ConfirmOrder',
    component: ConfirmOrder,
    auth: true
  },
  { path: '/MyOrder', name: 'MyOrder', component: MyOrder, auth: true },
  {
    path: '/AlreadyRefund',
    name: 'AlreadyRefund',
    component: AlreadyRefund,
    auth: true
  },
  {
    path: '/OrderDetails',
    name: 'OrderDetails',
    component: OrderDetails,
    auth: true
  },
  {
    path: '/ApplyRefund',
    name: 'ApplyRefund',
    component: ApplyRefund,
    auth: true
  },
  {
    path: '/CommonProblem',
    name: 'CommonProblem',
    component: CommonProblem,
    auth: true
  },
  {
    path: '/PaySuccess',
    name: 'PaySuccess',
    component: PaySuccess,
    auth: true
  },
  { path: '/Agreement', name: 'Agreement', component: Agreement, auth: true },
  { path: '/Test', name: 'Test', component: Test },
  {
    path: '/OrderCompleted',
    name: 'OrderCompleted',
    component: OrderCompleted,
    auth: true
  },
  {
    path: '/UniversityChoice',
    name: 'UniversityChoice',
    component: UniversityChoice,
    auth: true
  },{
    path: '/ChoosingRoles',
    name: 'ChoosingRoles',
    component: ChoosingRoles,
    auth: true
  },{
    path: '/PrimaryStudentRegister',
    name: 'PrimaryStudentRegister',
    component: PrimaryStudentRegister,
    auth: true
  }
];



