import React, { Component } from 'react';
import styles from './index.css';
import CSSModules from 'react-css-modules';
import Divider from '@material-ui/core/Divider';
import Modal from '@material-ui/core/Modal';
import ComponentsSwiperParent from '../Components/ComponentsSwiperParent';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class ParentHome extends Component {
  constructor(props) {
    super(props);
    document.title = '首页';
    this.state = {
      open: true,
      phoneListOpen: false,
      phoneList: [
        { name: '张小龙', number: 15145698754 },
        { name: '李小强', number: 13256984554 },
        { name: '张晓磊', number: 13245697896 }
      ]
    };
  }
  handleChange = name => event => {
    console.log(name);
    this.setState({
      [name]: event.target.value
    });
  };
  linkTo = () => {
    this.props.history.push('/ParentInformation');
  };
  getHelp = () => {
    this.setState({
      open: true
    });
  };
  closeModal = type => {
    this.setState({
      open: false
    });
  };
  openClassSalesman = () => {
    this.setState({
      phoneListOpen: true
    });
  };
  callSalesman = data => {
    console.log(data);
    // window.location.href = 'tel:' + data.number;
    this.setState({
      phoneListOpen: false
    });
  };
  componentDidMount(){
    console.log(localStorage.getItem('ParentHome_open'))
    if(localStorage.getItem('ParentHome_open')){
      this.setState({
        open : false
      })
    }else{
      this.setState({
        open : true
      })
      localStorage.setItem('ParentHome_open',true)
    }
  }
  render() {
    return (
      // <div styleName="App">
      //   hello vw-layout
      // </div>
      <div styleName="ParentHome">
        <div styleName="ParentHome_top">
          <div styleName="ParentHome_top_content">
            <div styleName="ParentHome_top_item">
              <span styleName="ParentHome_top_item_title">家长姓名</span>
              <span styleName="ParentHome_top_item_content">王昌龄</span>
              {/* <div styleName="ParentHome_top_item_line" /> */}
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="ParentHome_top_item">
              <span styleName="ParentHome_top_item_title">身份证号</span>
              <span styleName="ParentHome_top_item_content">
                230198756213215865
              </span>
              {/* <div styleName="ParentHome_top_item_line" /> */}
              <Divider style={{ backgroundColor: '#B5B5B6' }} />
            </div>
            <div styleName="ParentHome_top_item">
              <span styleName="ParentHome_top_item_title">联系电话</span>
              <span styleName="ParentHome_top_item_content">13265894587</span>
            </div>
          </div>
        </div>
        <ComponentsSwiperParent
          router={this.props}
          type={'Parent'}
          openClassSalesman={this.openClassSalesman}
        />
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.open}
          styleName="ParentHome_modal"
          onClick={() => {
            this.closeModal();
          }}
        >
          <div styleName="ParentHome_modalDiv">
            <span
              className="iconfont icon-xiangzuohuadong"
              styleName="ParentHome_button_icon"
            />
            <p styleName="ParentHome_modalDiv_span">学生信息可左右滑动</p>
          </div>
        </Modal>
        <Modal
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.phoneListOpen}
          styleName="ParentHome_modal"
        >
          <div styleName="phoneListModal">
            <div styleName="phoneListModal_content">
              {this.state.phoneList.map((v, i) => {
                return (
                  <div
                    styleName="ParentHome_top_item1"
                    key={i}
                    // onClick={() => this.callSalesman(v)}
                  >
                    <span styleName="ParentHome_top_item_title1">
                      {v.name}
                    </span>
                    <span styleName="ParentHome_top_item_content1">
                      {v.number}
                    </span>
                    <CopyToClipboard text={v.number} >
                      <div onClick={() => this.callSalesman(v)} styleName="copyBtn">复制</div>
                    </CopyToClipboard>
                    {/* <span
                      className="iconfont icon-xiangyoujiantou"
                      styleName="ParentHome_top_item_icon1"
                    /> */}
                    {
                      <Divider
                        style={{
                          backgroundColor:
                            i === this.state.phoneList.length - 1
                              ? "transparent"
                              : "#B5B5B6"
                        }}
                      />
                    }
                  </div>
                );
              })}
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}

export default CSSModules(ParentHome, styles);



